import React, { Suspense, useState } from 'react';
import styled from '@emotion/styled';

import { LyricsContainer } from 'features/songs/details/SongDetailsSkeleton';
import { LyricsDisplaySkeleton } from './LyricsDisplaySkeleton';
import { LyricsSource } from './useLyrics';
import { css } from '@emotion/react';

const LyricsDisplay = React.lazy(() => import('../lyrics/LyricsDisplay'));

interface LyricsSectionProps {
  sources: LyricsSource[];
}

function formatLocale(locale: string): string {
  switch (locale) {
    case 'zh_HK':
      return '繁體中文';
    case 'zh_HK_R':
      return '粵語拼音';
    default:
      return '未知語言';
  }
}

export function LyricsSection({ sources }: LyricsSectionProps) {
  const [selected, setSelected] = useState(0);

  return (
    <LyricsContainer>
      <LyricsLanguageMenu>
        {sources.map((source, i) => (
          <LyricsLanguageMenuItem
            key={source.locale}
            active={selected === i}
            onClick={() => setSelected(i)}
          >
            {formatLocale(source.locale)}
          </LyricsLanguageMenuItem>
        ))}
      </LyricsLanguageMenu>

      <Suspense fallback={<LyricsDisplaySkeleton />}>
        <LyricsDisplay source={sources[selected]} />
      </Suspense>
    </LyricsContainer>
  );
}

export default LyricsSection;

const LyricsLanguageMenu = styled.ul`
  list-style: none;
  display: flex;
  margin: 0 0 20px 0;
  padding: 0;
`;

interface LyricsLanguageMenuItemProps {
  active: boolean;
}

const LyricsLanguageMenuItem = styled.li<LyricsLanguageMenuItemProps>`
  padding: 10px 0;
  margin-right: 15px;
  cursor: pointer;
  ${(props) =>
    props.active &&
    css`
      font-weight: 800;
      border-bottom: 3px solid #fff;
    `};
`;
